.header {
    height: 60px;
    display: flex;
    align-items: center;
    background-color:black;
    position: sticky;
    top: 0;
    z-index:100;
}

.header__logo {
    width: 120px;
    object-fit:contain;
    margin-top:21px;
    margin-left:21px;

}
.header__search{
    display:flex;
    flex:1;
    align-items:center;
    border-radius: 24px;
}
.header__searchInput {
    height: 12px;
    padding: 10px;
    border:none;
    width:100%;
    margin-left: 10px;
}

.header__searchIcon {
    padding: 4.8px;
    height: 21px;
    background-color:#cd9042;
    
}

.header__nav {
    display: flex;
    justify-content: space-evenly;

}
.header__option {
    display:flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right:10px;
    color:white;

}
.header__optionLineOne {
    font-size: 10px;
}
.header__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket {
    display:flex;
    align-items:center;
    color:white;
}

.header__basketCount {
    margin-left: 10px;
    margin-right:10px ;
}
