.login {
    background-color: white;
    height: 100vh;
    align-items: center;
    display: flex;
    flex-direction: column;
    
}
.login__container {
    width: 300px;
    height:fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border:2px solid lightgray;
    padding: 20px;

}
.login__container > h1 {
    font-weight:500;
    margin-bottom: 20px;
    
}
.login__container >form >h5 {
    margin-bottom: 5px;
}
.login__container >form >input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width:98%;
}
.login__container > p {
    margin-top: 15px;
    font-size:12px;
    
}
.login__signInButton {
    background:#f0c14b;
    border-radius: 2px;
    width:100%;
    height: 30px;
    border:1px solid;
    margin-top:10px;
    border-color: #a88734 #9c7e31 #846a29;
}
.login__registerButton {
    border-radius:2px;
    width:100%;
    height: 30px;
    border:1px solid;
    margin: top 10px;
    border-color:darkgray;
}

.login__container > h4 {
    font-size:10px;
    font-weight: 500;
    margin-left: 100px;
    margin-bottom: 10px;
    
}

.login__logo {
    margin-bottom:20px;
    margin-top:20px;
    object-fit: contain;
    width: 100px;
    margin-left: auto;
    margin-right:auto;
}